.font-h1 {
  @include font-config($size: 4.8rem, $weight: bold, $lh: 5.6rem);

  @include mq("phablet") {
    @include font-config($size: 3.2rem, $lh: 4rem);
  }
}

.font-h2 {
  @include font-config($size: 3.2rem, $weight: bold, $lh: 4rem);

  @include mq("phablet") {
    @include font-config($size: 2.8rem, $lh: 3.6rem);
  }
}

.font-h3 {
  @include font-config($size: 2.4rem, $weight: 500, $lh: 3.2rem);

  @include mq("phablet") {
    @include font-config($size: 2rem, $lh: 2.8rem);
  }
}

.font-h4 {
  @include font-config($size: 1.8rem, $weight: 500, $lh: 2.8rem);
}

.font-lead {
  @include font-config($size: 2.4rem, $lh: 3.2rem);

  @include mq("phablet") {
    @include font-config($size: 2rem, $lh: 2.8rem);
  }
}

.font-bigger {
  @include font-config($size: 1.8rem, $lh: 2.8rem);
}

.font-main {
  @include font-config($size: 1.6rem, $lh: 2.4rem);
}

.font-smaller {
  @include font-config($size: 1.4rem, $lh: 2rem);
}
